/****************************************************************************************/
/* Client Specific Styling for Wells Lamont
/****************************************************************************************/
@import "client_variables";

body {
  color: $gray-900;
  font-family: $font-family-base;
}

p {
  line-height: 2;
}

a {
  color: $dark;
}

body.page {
  background: rgb(0, 0, 0);
  background: linear-gradient(
    180deg,
    rgba(0, 0, 0, 0.8673844537815126) 1%,
    rgba(0, 0, 0, 0.667) 5%,
    rgba(0, 0, 0, 0) 13%
  );
  background-attachment: fixed;
  background-repeat: no-repeat;
}

a:hover {
  color: $link-hover-color;
}

.c9 .wrapper {
  margin-top: -115px;
  margin-bottom: 0px;
}

.single-post .wrapper {
  margin-top: 0px;
}

@media only screen and (min-width: 768px) {
  .blog .c9 .wrapper {
    margin-top: 30px;
  }
}

.c9-layout-column-wrap {
  display: -ms-grid;
}

.c9-grid .c9-layout-column-wrap {
  position: relative;
}

@media only screen and (max-width: 667px) {
  .single .entry-content {
    padding: 20px;
  }

  .page-search-results {
    margin-top: 135px;
  }

  .blog .site .wrapper {
    margin-top: 30px;
  }
}

@media only screen and (min-width: 1200px) {
  .navbar .container {
    width: 1340px;
    max-width: 1340px;
  }
  #left-sidebar,
  #right-sidebar {
    background-color: $light;
  }
}

