/****************************************************************************************/
/* Drop down menus, animations of menus, and button hover animations in main nav */
/****************************************************************************************/
.header-navbar .navbar .nav .nav-item .dropdown-item {
  padding: 10px 8px;
}

.header-navbar .navbar .nav .nav-item .dropdown-menu .nav-item {
  margin: 0 6px;
}

.header-navbar .nav .nav-item .dropdown-menu {
  border-radius: 0px;
  margin-top: 1em;
  border: 0px;
  -webkit-animation: fadeInFromNone 0.5s ease-out;
  animation: fadeInFromNone 0.5s ease-out;
}

.header-navbar .nav .nav-item .dropdown-menu.show {
  display: block;
  -webkit-animation: fadeInFromNone 0.5s ease-out;
  animation: fadeInFromNone 0.5s ease-out;
}

@-webkit-keyframes fadeInFromNone {
  0% {
    display: none;
    opacity: 0;
  }

  1% {
    display: block;
    opacity: 0;
  }

  100% {
    display: block;
    opacity: 1;
  }
}

@keyframes fadeInFromNone {
  0% {
    display: none;
    opacity: 0;
  }

  1% {
    display: block;
    opacity: 0;
  }

  100% {
    display: block;
    opacity: 1;
  }
}

/* first level nav link hover effects */
.header-navbar .nav .nav-item > .nav-link,
.header-navbar .nav .nav-item .nav-link > span,
.navbar:not(.navbar-small)
  #mega-menu-wrap-primary
  #mega-menu-primary
  > li.mega-menu-item
  > a.mega-menu-link,
.navbar:not(.navbar-small)
  #mega-menu-wrap-primary
  #mega-menu-primary
  > li.mega-menu-item
  > a.mega-menu-link
  > span,
.navbar-small.fixed-top
  #mega-menu-wrap-primary
  #mega-menu-primary
  > li.mega-menu-item
  > a.mega-menu-link,
.navbar-small.fixed-top
  #mega-menu-wrap-primary
  #mega-menu-primary
  > li.mega-menu-item
  > a.mega-menu-link
  > span {
  display: inline-block;
  overflow: hidden;
  color: $light;
  padding: 0px;

  &:before,
  &:after {
    right: 0;
    bottom: 0;
    background: darken($primary, 10%);
  }

  &:before {
    width: 100%;
    height: 2px;
    transform: translateX(-100%);
  }

  &:after {
    width: 2px;
    height: 100%;
    transform: translateY(100%);
  }

  > span {
    display: block;
    padding: 14px 12px;

    &:before,
    &:after {
      left: 0;
      top: 0;
      background: darken($primary, 10%);
    }

    &:before {
      width: 100%;
      height: 2px;
      transform: translateX(100%);
    }

    &:after {
      width: 2px;
      height: 100%;
      transform: translateY(-100%);
    }
  }

  &:focus:before,
  &:focus:after,
  &:focus > span:before,
  &:focus > span:after,
  &:hover:before,
  &:hover:after,
  &:hover > span:before,
  &:hover > span:after {
    transform: translate(0, 0);
    transition: transform 0.4s ease-in-out;
  }
}

.header-navbar .nav .nav-item .nav-link,
.header-navbar .nav .nav-item .nav-link > span,
.navbar
  #mega-menu-wrap-primary
  #mega-menu-primary
  > li.mega-menu-item
  > a.mega-menu-link,
.navbar
  #mega-menu-wrap-primary
  #mega-menu-primary
  > li.mega-menu-item
  > a.mega-menu-link
  > span,
.navbar-small.fixed-top
  #mega-menu-wrap-primary
  #mega-menu-primary
  > li.mega-menu-item
  > a.mega-menu-link,
.navbar-small.fixed-top
  #mega-menu-wrap-primary
  #mega-menu-primary
  > li.mega-menu-item
  > a.mega-menu-link
  > span {
  position: relative;
  text-decoration: none;

  &:before,
  &:after {
    content: "";
    position: absolute;
  }
}

.navbar-small.fixed-top
  #mega-menu-wrap-primary
  #mega-menu-primary
  > li.mega-menu-item
